<template>
    <div>
        <cxlt-toastr :show="show"
                     :toastr="toastr"
                     @show-change="showChange"></cxlt-toastr>
        <button @click="switchShow">click me</button>
    </div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
            show: false,
            toastr: {
                // title: '',
                // message: '',
                // position: 'top left'
                title: ''
            }
        }
    },
    methods: {
        switchShow() {
            this.show = !this.show
            // this.toastr = {
            //     title: '',
            //     message: ''
            // }
            this.toastr.title = ''
            this.toastr.message = ''
        },
        showChange(show) {
            if (this.show !== show) {
                this.show = show
            }
        }
    }
}
</script>



